import React from 'react';
import Button from './../../../../Components/Button'
import Timer from "./../../../HomePage/SliderSection/SimpleSlider/TournamentItem/Timer/Timer"
import classNames from 'classnames';


import iconChestOpen from "../../../../Components/images/bonuses/chest-open.svg";
import iconChips from "../../../../Components/images/bonuses/chips.png";

class ChestItem extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            prize: null,
            animationChest: false,
            isAccess: this.props.isAccess,
        };

        this.openChest = this.openChest.bind(this);

    }


    openChest = () => {

        const bonusVals = [200, 300, 500,];
        const bonusVal = bonusVals[Math.floor(Math.random() * bonusVals.length)];

        this.setState({animationChest: true}); /// add animation css class

        //const currentTime = new Date();

        /* TODO AJAX if everything good
        /   –ï—Å–ª–∏ –≤—Å–µ —Ö–æ—Ä–æ—à–æ —Ç–æ –¥–ª—è —Ç–µ–∫—É—â–µ–≥–æ –±–æ–Ω—É–Ω–∞ –¥–æ–ª–∂–µ–Ω  –æ–±–Ω–æ–≤–∏—Ç—å—Å—è  bonusesLastUpdate = currentTime
        /   bonusesNextAccess + 24 —á–∞—Å–∞ –æ—Ç —Ç–µ–∫—É—â–µ–π –¥–∞—Ç—ã –∑–∞–¥–∞–µ—Ç—å—Å—è –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ –µ—Å–ª–∏ –≤—Å–µ –æ–∫ ...
        */

        setTimeout(() => {
            this.setState({
                prize: bonusVal,
            });
            this.props.addCoins(bonusVal);
        }, 2100);

        ////// –û–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–∞—Ç—É—Å —Ç–µ–∫—É—â–µ–≥–æ —Å—É–Ω–¥—É–∫–∞
        setTimeout(()=>{
            this.setState({
                prize:false,
                animationChest: false,
            });

            this.props.addBonusesCounter();



            // this.props.addBonusesOpeningPrice(100);
            // this.props.removeCoins(100);

        }, 2000);

        return bonusVal
    };






    render() {
        const iconChest = this.props.iconChest;
        let bonusStatus = this.state.isAccess;

        let openingPrice = this.props.bonusesOpeningPrice;

        let bonusLastUpdate = this.props.bonusesLastUpdate;
        let bonusNextAccess = this.props.bonusesNextAccess;


        const itemStyles = classNames("circularSlider_item", {'closed': !bonusStatus});
        const chestStyles = classNames("chestBox_img chestBox_close", {"active": this.state.animationChest});

        // console.log("openingPrice = ", openingPrice);

        return (

            <div className={itemStyles}>

                    {this.state.prize ?
                        (
                            <div className="chestBox">
                                <div className="chestBox_prize">
                                    <p className="chestBox_text">+{this.state.prize} –§–ò–®–ï–ö</p>
                                    <img className="chestBox_chips" src={iconChips} alt="—Ñ–∏—à–∫–∏"/>
                                </div>
                                <img className="chestBox_img chestBox_open" src={iconChestOpen} width={190}
                                     height={200} alt="–±–æ–Ω—É—Å"/>
                            </div>
                        )
                        : (<div className="chestBox"><img className={chestStyles} src={iconChest} width={148} height={142} alt="–±–æ–Ω—É—Å"/></div>)
                    }

                {bonusStatus ?

                    (<Button className="btn btn_chest" onClick={this.openChest}>
                        <span>–û—Ç–∫—Ä—ã—Ç—å</span>
                        {
                            (openingPrice > 0) ? <span className="btn_chest__val">{openingPrice}</span> : null
                        }

                    </Button>)
                    :
                    (
                        <div className="chest_timer">
                            <Timer timerStart={bonusLastUpdate} timerEnd={bonusNextAccess}
                                   timerText={"–ë—É–¥–µ—Ç –¥–æ—Å—Ç—É–ø–Ω–æ —á–µ—Ä–µ–∑:"}/>
                        </div>
                    )

                }
            </div>
        )

    }


}


export default ChestItem