import React from 'react';
import Button from './../../../Components/Button'
import Timer from "../../HomePage/SliderSection/SimpleSlider/TournamentItem/Timer/Timer";
import classNames from 'classnames';

function ChestsTopItem({iconChest, bonusesStatus, chestIndex}) {

    const infoChest = bonusesStatus[chestIndex];
    const chestAccess = infoChest.isAccess;

    const tStart = infoChest.bonusesLastUpdate;
    const tEnd = infoChest.bonusesNextAccess;

    const chestsTopStyles = classNames("chestsTop_btn", {'closed': !chestAccess});

    function OpenChest() {
        alert("OK");
    }

    return (
        <li className="chestsTop_item">
            {
                chestAccess ? (
                    <Button className="chestsTop_btn" onClick={(e) => OpenChest(e)}>
                        <img className="chestsTop_img" src={iconChest} alt="—Å—É–Ω–¥—É–∫"/>
                        <p className="chestsTop_text">–ë–µ—Å–ø–ª–∞—Ç–Ω–æ</p>
                    </Button>
                ) : (
                    <div className={chestsTopStyles}>
                        <img className="chestsTop_img" src={iconChest} alt="—Å—É–Ω–¥—É–∫"/>
                        <Timer timerStart={tStart} timerEnd={tEnd} timerText={"–ë—É–¥–µ—Ç –¥–æ—Å—Ç—É–ø–µ–Ω —á–µ—Ä–µ–∑:"}
                               hideTimerRank={true}/>
                    </div>
                )
            }
        </li>
    )
}


export default ChestsTopItem