import React from "react"
import Countdown from "react-countdown";

function Timer(props){

    const {timerStart, timerEnd, timerText, hideTimerRank} = props;

    const q100 = Date.parse(timerEnd) - Date.parse(timerStart);
    const qNow = Date.now() - Date.parse(timerStart);

    let procForRank  = `${ (qNow * 100 / q100).toFixed(2) }%`;

    let scope = {
        splitterStyle: {
            width: procForRank,
        }
    };

    const renderer = ({days, hours, minutes, seconds, completed }) => {
        if (completed) {
            // Render a complete state
            return (
                <span>0–¥ : 0—á : 00–º : 00—Å</span>
            );
        } else {
            // Render a countdown
            return (
                <span>{days}–¥ :{hours}—á : {minutes}–º : {seconds}—Å</span>
            );
        }
    };




    return(
        <div className="timer">
            <div className="timer_block">
                <span className="timer_name">{timerText}</span>
                <p className="timer_value">
                    <Countdown date={  new Date(timerEnd)} zeroPadDays={2} renderer={renderer} />
                </p>
            </div>
            {
                hideTimerRank ? null : ( <div className="timer_rank"><div className="timer_rank__line" style={scope.splitterStyle} /></div>)
            }
        </div>
    )


}

export default Timer