import React from 'react';
import "./chestsTopList.scss"
import ChestsTopItem from "./ChestsTopItem";

function ChestsTopList ({iconChest, bonusesStatus}){

    return(
        <ul className="chestsTop_list">
            <ChestsTopItem iconChest={iconChest} bonusesStatus={bonusesStatus} chestIndex={1} key={0} />
            <ChestsTopItem iconChest={iconChest} bonusesStatus={bonusesStatus} chestIndex={2} key={1} />
            <ChestsTopItem iconChest={iconChest} bonusesStatus={bonusesStatus} chestIndex={3} key={2} />
        </ul>
    )
}



export default ChestsTopList


